.card-container {
  margin: 10px;
  height: 150px;
  min-width: 100px;
  width: 10%;
  background-color: grey;
  border: 6px solid grey;
  border-radius: 25px;
  display: inline-block;
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
}



